import React from 'react';

import NavbarContainer from '../containers/NavbarContainer';

const Index = (props) => {
  return (
    <div>
      <NavbarContainer />
      <div >

        {
          props.children && React.cloneElement(props.children, props)
        }

      </div>
    </div>

  );
};

export default Index;



